<template>
  <div>
    <div id="header" class="content">
      <header-nav></header-nav>
    </div>
    <div class="content padding-top-100">
      <el-row class="demo-avatar demo-basic">
        <el-col>
          <div class="demo-basic--circle">
            <div class="block wb100 clearfix">
              <el-avatar class="left user-img-boder" :size="100"
                         :style="{ backgroundImage: 'url(' + osshost +userinfo.imgurl+userface+ ')' }"></el-avatar>
              <div class="left uright ml15 mt15">
                <h3>{{userinfo.nickname}}</h3>
                <p v-if="!userid||userinfo.ismyown"><span class="mr30">{{userinfo2.address}}</span>生日：<span>{{userinfo2.birthday}}</span>
                </p>
                <div class="tag-group" v-if="userinfo.tag">
                  <el-tag effect="plain" type="success" size="small">{{userinfo.tag}}</el-tag>
                </div>
              </div>
              <div class="left ml30 mt20">
                <el-button @click="edituserinfo()" v-if="!userid||userinfo.ismyown">编辑信息</el-button>
                <div v-else>
                  <el-button class="bg-yellow" v-if="userinfo.relationship == 0 || userinfo.relationship == 2"
                             @click="attentionUser(userid)">关注
                  </el-button>
                  <el-button v-else @click="unAttentionUser(userid)">已关注</el-button>
                </div>

              </div>
              <div class="right" v-if="!userid||userinfo.ismyown">
                <div class="uinfonmb">
                  <p><span>{{userinfo.fans}}</span><span>粉丝</span></p>
                  <p><span>{{userinfo.follows}}</span><span>关注</span></p>
                </div>
              </div>
            </div>
            <div class="upersoninfo mt15 ml10 wb50">{{userinfo.personalprofile}}</div>
          </div>
        </el-col>
      </el-row>

      <el-tabs class="mt20" v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="总览" name="first">
          <div class="ul_title mt10">
            <div class="clearfix">
              <h2 class="title mt10">主题{{userinfo.topics}}</h2>
            </div>
          </div>
          <div class="padding-top-60 mb30 text-center" v-if="themeFList.length==0">
            <el-avatar shape="square" :size="200"  :src="nullUrl"></el-avatar>
            <div>当前还没有主题哦~</div>
          </div>
          <ul class="theme-list clearfix" v-else>
            <li class="item" v-for="(item,index) in themeFList" :key="index" @click="themeDetail(item.topicid)">
              <div class="cover br5 bg-img xl"
                   :style="{ backgroundImage: 'url(' + osshost + item.topicimg +detailpic+ ')' }">
              </div>
              <div class="bg-sd">
                <div class="name text-tips">{{ item.title }}</div>
                <div class="author">
                  <span class="left text-xm el-icon-star-off margin-right-xs block">{{ item.collections }}</span>
                </div>
              </div>
            </li>
          </ul>
          <div class="ul_title mt20">
            <div class="clearfix">
              <h2 class="title mt10">食谱{{userinfo.recipes}}</h2>
            </div>
          </div>
          <div class="padding-top-60 mb30 text-center" v-if="menuFList.length==0">
            <el-avatar shape="square" :size="200"  :src="nullUrl"></el-avatar>
            <div>当前还没有动态哦~</div>
          </div>
          <ul class="like-list3 clearfix" v-else>
            <li class="item" v-for="(item, index) in menuFList" :key="index" @click="bookDetail(item.id)">
              <div class="cover br5 bg-img" :style="{ backgroundImage: 'url(' + osshost +item.faceimg +listpic+ ')' }">
              </div>
              <div>
                <div class="name text-tips">{{ item.title }}</div>
                <div class="author">
                  <span class="left text-xm el-icon-star-off margin-right-xs block">{{ item.keepnum }}</span>
                </div>
              </div>
            </li>
          </ul>
          <div class="ul_title mt20">
            <div class="clearfix">
              <h2 class="title mt10">动态{{userinfo.moments}}</h2>
            </div>
          </div>
          <div class="padding-top-60 mb30 text-center" v-if="dtFList.length==0">
            <el-avatar shape="square" :size="200" :src="nullUrl"></el-avatar>
            <div>当前还没有动态哦~</div>
          </div>
          <ul class="like-list3 clearfix mb30" v-else>
            <li class="item" v-for="(item, index) in dtFList" :key="index" @click="actionDetail(item.id)">
              <div class="cover br5 bg-img"
                   :style="{ backgroundImage: 'url(' + osshost +item.imgurllist +listpic+ ')' }">
              </div>
              <div>
                <div class="name text-tips">{{ item.dtinfo }}</div>
                <div class="author">
                  <span class="left text-xm el-icon-lollipop margin-right-xs block">{{ item.cnum }}</span>
                </div>
              </div>
            </li>
          </ul>
        </el-tab-pane>
        <el-tab-pane :label="'主题'+userinfo.topics" name="second">
          <div class="padding-top-60 mb30 text-center" v-if="themeList.length==0">
            <el-avatar shape="square" :size="200" :src="nullUrl"></el-avatar>
            <div>当前还没有主题哦~</div>
          </div>
          <ul class="theme-list clearfix mb30" v-else>
            <li class="item" v-for="(item,index) in themeList" :key="index" >
              <div class="cover br5 bg-img xl"
                   :style="{ backgroundImage: 'url(' + osshost + item.topicimg +detailpic+ ')' }" @click="themeDetail(item.topicid)">
              </div>
              <div class="bg-sd">
                <div class="name text-tips">{{ item.title }}</div>
                <div class="author">
                  <span class="left text-xm el-icon-star-off margin-right-xs block">{{ item.collections }}</span>
                </div>
              </div>
              <el-button class="btn-bj" v-if="!userid||userinfo.ismyown" @click="editTheme(item.topicid)" type="primary"
                         icon="el-icon-edit" circle></el-button>
              <el-button class="btn-sc2" v-if="!userid||userinfo.ismyown" @click="deleteTheme(item.topicid,index)"
                         type="danger"
                         icon="el-icon-delete" circle></el-button>
            </li>
          </ul>
          <infinite-loading @infinite="infiniteTheme" class="mt50 mb60">
            <div slot="spinner"><i class="el-icon-loading"></i>小弟拼命加载中...</div>//加载中的文字
            <div slot="no-more" class="el-icon-food grayColor">已加载完毕!</div>//加载完毕的文字
            <div slot="no-results" class="el-icon-coffee-cup grayColor">暂无数据:(</div>//没有数据的文字
          </infinite-loading>
        </el-tab-pane>
        <el-tab-pane :label="'食谱'+userinfo.recipes" name="third">
          <div class="padding-top-60 mb30 text-center" v-if="menuList.length==0">
            <el-avatar shape="square" :size="200" :src="nullUrl"></el-avatar>
            <div>当前还没有食谱哦~</div>
          </div>
          <ul class="like-list3 clearfix mb30" v-else>
            <li class="item" v-for="(item, index) in menuList" :key="index">
              <div class="cover br5 bg-img" :style="{ backgroundImage: 'url(' + osshost +item.faceimg +listpic+ ')' }"
                   @click="bookDetail(item.id)">
              </div>
              <div>
                <div class="name text-tips">{{ item.title }}</div>
                <div class="author">
                  <span class="left text-xm el-icon-star-off margin-right-xs block">{{ item.keepnum }}</span>
                </div>
              </div>
              <el-button class="btn-bj" v-if="!userid||userinfo.ismyown" @click="editDetail(item.id)" type="primary"
                         icon="el-icon-edit" circle></el-button>
              <el-button class="btn-sc2" v-if="!userid||userinfo.ismyown" @click="deleteCp(item.id,index)" type="danger"
                         icon="el-icon-delete" circle></el-button>
            </li>
          </ul>
          <infinite-loading @infinite="infiniteHandler" class="mt50 mb60">
            <div slot="spinner"><i class="el-icon-loading"></i>小弟拼命加载中...</div>//加载中的文字
            <div slot="no-more" class="el-icon-food grayColor">已加载完毕!</div>//加载完毕的文字
            <div slot="no-results" class="el-icon-coffee-cup grayColor">暂无数据:(</div>//没有数据的文字
          </infinite-loading>
        </el-tab-pane>
        <el-tab-pane :label="'动态'+userinfo.moments" name="four">
          <div class="padding-top-60 mb30 text-center" v-if="dtList.length==0">
            <el-avatar shape="square" :size="200" :src="nullUrl"></el-avatar>
            <div>当前还没有动态哦~</div>
          </div>
          <ul class="like-list2 clearfix mb30" v-else>
            <li class="item" v-for="(item, index) in dtList" :key="index">
              <div class="cover br5 bg-img"
                   :style="{ backgroundImage: 'url(' + osshost +item.imgurllist +listpic+ ')' }"
                   @click="actionDetail(item.id)">
              </div>
              <div>
                <div class="name text-tips">{{ item.dtinfo }}</div>
                <div class="author">
                  <span class="left text-xm el-icon-lollipop margin-right-xs block">{{ item.cnum }}</span>
                </div>
              </div>
              <el-button class="btn-sc2" v-if="!userid||userinfo.ismyown" @click="deleteAction(item.id,index)"
                         type="danger"
                         icon="el-icon-delete" circle></el-button>
            </li>
          </ul>
          <infinite-loading @infinite="infiniteDT" class="mt50 mb60">
            <div slot="spinner"><i class="el-icon-loading"></i>小弟拼命加载中...</div>//加载中的文字
            <div slot="no-more" class="el-icon-food grayColor">已加载完毕!</div>//加载完毕的文字
            <div slot="no-results" class="el-icon-coffee-cup grayColor">暂无数据:(</div>//没有数据的文字
          </infinite-loading>
        </el-tab-pane>

        <el-tab-pane label="收藏的食谱" name="five" v-if="!userid||userinfo.ismyown">
          <div class="padding-top-60 mb30 text-center" v-if="scmenuList.length==0">
            <el-avatar shape="square" :size="200" :src="nullUrl"></el-avatar>
            <div>当前还没有收藏食谱哦~</div>
          </div>
          <ul class="like-list3 clearfix mb30" v-else>
            <li class="item" v-for="(item, index) in scmenuList" :key="index">
              <div class="cover br5 bg-img" :style="{ backgroundImage: 'url(' + osshost +item.cpimg +listpic+ ')' }"
                   @click="bookDetail(item.cpid)">
              </div>
              <div>
                <div class="name text-tips">{{ item.title }}</div>
                <div class="author">
                  <img :src="osshost + item.faceimg +userface" class="headicon br50">
                  <span class="nickname text-lips" style="max-width: 238px">{{item.nickname}}</span>
                  <span class="right text-xm el-icon-star-off margin-right-xs block">{{ item.collections }}</span>
                </div>
              </div>
            </li>
          </ul>
          <infinite-loading @infinite="infinitesccp" class="mt50 mb60">
            <div slot="spinner"><i class="el-icon-loading"></i>小弟拼命加载中...</div>//加载中的文字
            <div slot="no-more" class="el-icon-food grayColor">已加载完毕!</div>//加载完毕的文字
            <div slot="no-results" class="el-icon-coffee-cup grayColor">暂无数据:(</div>//没有数据的文字
          </infinite-loading>
        </el-tab-pane>

        <el-tab-pane label="收藏的主题" name="six" v-if="!userid||userinfo.ismyown">
          <div class="padding-top-60 mb30 text-center" v-if="scthemeList.length==0">
            <el-avatar shape="square" :size="200" :src="nullUrl"></el-avatar>
            <div>当前还没有收藏主题哦~</div>
          </div>
          <ul class="theme-list clearfix mb30" v-else>
            <li class="item" v-for="(item,index) in scthemeList" :key="index" >
              <div class="cover br5 bg-img xl"
                   :style="{ backgroundImage: 'url(' + osshost + item.topicimg +detailpic+ ')' }" @click="themeDetail(item.topicid)">
              </div>
              <div class="bg-sd">
                <div class="name text-tips">{{ item.title }}</div>
                <div class="author">
                  <img :src="osshost + item.faceimg + userface" class="headicon br50">
                  <span class="nickname text-lips" style="max-width: 150px">{{ item.nickname }}</span>
                  <span class="right text-xm el-icon-star-off margin-right-xs block">{{ item.collections }}</span>
                </div>
              </div>
            </li>
          </ul>
          <infinite-loading @infinite="infinitesczt" class="mt50 mb60">
            <div slot="spinner"><i class="el-icon-loading"></i>小弟拼命加载中...</div>//加载中的文字
            <div slot="no-more" class="el-icon-food grayColor">已加载完毕!</div>//加载完毕的文字
            <div slot="no-results" class="el-icon-coffee-cup grayColor">暂无数据:(</div>//没有数据的文字
          </infinite-loading>
        </el-tab-pane>

      </el-tabs>
    </div>
    <footer-bottom></footer-bottom>
    <Totop></Totop>
  </div>
</template>

<script>
  import headerNav from "@/components/headerNav";
  import ThemeItem from "@/components/themeItem.vue";
  import FoodItem from "@/components/foodItem.vue";
  import Totop from '@/components/totop.vue';
  import FooterBottom from "@/components/footer";
  import InfiniteLoading from 'vue-infinite-loading';

  import {getActionList, delDt} from '@/api/action'
  import {getMenuList, delCp} from '@/api/menu'
  import {getSelfThemeList, getUserThemeList, delTheme} from '@/api/theme'
  import {followUser, unfollowUser} from '@/api/operate'
  import {getMyInfo, getUserInfo,getMyspacetheme,getMyspacecp} from '@/api/user'
  import {Message} from "element-ui";

  export default {
    components: {
      headerNav,
      ThemeItem,
      FoodItem,
      FooterBottom,
      InfiniteLoading,
      Totop
    },
    data() {
      return {
        userid: '',
        token: '',
        nullUrl: '../../static/dataNull.png',
        circleUrl: "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
        sizeList: ["large", "medium", "small"],
        activeName: 'first',
        userinfo: '',
        userinfo2: '',
        menuList: [],
        menuFList: [],
        scmenuList:[],
        scthemeList: [],
        scmenuFList:[],
        scthemeFList: [],
        themeList: [],
        themeFList: [],
        dtList: [],
        dtFList: [],
        page: 0,
        listpic: '?x-oss-process=style/list-img',
        userface: '?x-oss-process=style/user-img',
        detailpic: '?x-oss-process=style/detail-img',
        osshost: process.env.UPLOAD_URL,
      }
    },
    created() {
      this.token = this.$store.state.user.token || '';
    },
    mounted() {
      this.userid = this.$route.query.id || 0;
      if (this.userid != 0) {
        this.getfriendInfo();
        this.page++;
        this.getMenu();
        this.getDtlist();
        this.getUserThemeList();
      } else {
        this.getUser();
        this.page++;
        this.getMenu();
        this.getDtlist();
        this.getThemeList();
        this.getscThemeList();
        this.getscMenu();
      }
    },
    methods: {
      handleClick(tab, event) {
        this.activeName = tab.name;
        this.page = 0;

        if (tab.name == "second") {
          this.themeList = [];
          this.infiniteTheme();
        } else if (tab.name == "third") {
          this.menuList = [];
          this.infiniteHandler();
        } else if (tab.name == "four") {
          this.dtList = [];
          this.infiniteDT();
        }
        else if (tab.name == "five") {
          this.scmenuList = [];
          this.infinitesccp();
        }else if (tab.name == "six") {
          this.scthemeList = [];
          this.infinitesczt();
        }
      },
      getUser() {
        getMyInfo().then(response => {
          let res = response.data;
          if (res.success) {
            this.userinfo = res.result;
            this.getUserInfo()
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      getUserInfo() {
        this.$store.dispatch('GetUserInfo').then(response => {
          this.userinfo2 = response.data.result;
        }).catch(error => {
        })
      },
      getfriendInfo() {
        getUserInfo(this.userid).then(response => {
          let res = response.data;
          if (res.success) {
            this.userinfo = res.result;
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      infiniteHandler($state) {
        this.page++;
        this.getMenu($state);
      },
      getMenu($state) {
        getMenuList(this.userid, this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length != 0) {
              if (this.page == 1) {
                this.menuFList = res.result
              }
              this.menuList = this.menuList.concat(res.result); //追加新数据

              if (this.page != 0) {
                $state.loaded();
              }
            } else {
              $state.complete(); //这里是加载了数据，设置不在加载
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      infiniteTheme($state) {
        this.page++;
        if (this.userid != 0) {
          this.getUserThemeList($state)
        } else {
          this.getThemeList($state)
        }
      },
      getThemeList($state) {
        getSelfThemeList(this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length != 0) {
              if (this.page == 1) {
                this.themeFList = res.result
              }
              this.themeList = this.themeList.concat(res.result); //追加新数据

              if (this.page != 0) {
                $state.loaded();
              }
            } else {
              $state.complete(); //这里是加载了数据，设置不在加载
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      getUserThemeList($state) {
        getUserThemeList(this.userid, this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length != 0) {
              if (this.page == 1) {
                this.themeFList = res.result
              }
              this.themeList = this.themeList.concat(res.result); //追加新数据

              if (this.page != 0) {
                $state.loaded();
              }
            } else {
              $state.complete(); //这里是加载了数据，设置不在加载
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      infiniteDT($state) {
        this.page++;
        this.getDtlist($state);
      },
      getDtlist($state) {
        getActionList(this.userid, this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length != 0) {
              this.dtList = this.dtList.concat(res.result); //追加新数据
              for (var i = 0; i < this.dtList.length; i++) {
                this.dtList[i].imgurllist = this.dtList[i].imgurllist.split('|');
                this.dtList[i].imgurllist = this.dtList[i].imgurllist[0];
              }
              if (this.page == 1) {
                this.dtFList = res.result;
                for (var i = 0; i < this.dtFList.length; i++) {
                  this.dtFList[i].imgurllist = this.dtFList[i].imgurllist.split('|');
                  this.dtFList[i].imgurllist = this.dtFList[i].imgurllist[0];
                }
              }
              if (this.page != 0) {
                $state.loaded();
              }
            } else {
              $state.complete(); //这里是加载了10页数据，设置不在加载
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      infinitesccp($state) {
        this.page++;
        this.getscMenu($state);
      },
      getscMenu($state) {
        getMyspacecp(2, this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length != 0) {
              if (this.page == 1) {
                this.scmenuFList = res.result
              }
              this.scmenuList = this.scmenuList.concat(res.result); //追加新数据

              if (this.page != 0) {
                $state.loaded();
              }
            } else {
              $state.complete(); //这里是加载了数据，设置不在加载
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      infinitesczt($state) {
        this.page++;
        this.getscThemeList($state);
      },
      getscThemeList($state) {
        getMyspacetheme(3, this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length != 0) {
              if (this.page == 1) {
                this.scthemeFList = res.result
              }
              this.scthemeList = this.scthemeList.concat(res.result); //追加新数据

              if (this.page != 0) {
                $state.loaded();
              }
            } else {
              $state.complete(); //这里是加载了数据，设置不在加载
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      edituserinfo() {
        this.$router.push({
          path: '/userinfo'
        })
      },
      attentionUser(userid) {
        if (this.token) {
          followUser(userid).then(response => {
            let res = response.data;
            if (res.success) {
              if (this.userinfo.relationship == 0) {
                this.userinfo.relationship = 1;
              } else if (this.userinfo.relationship == 2) {
                this.userinfo.relationship = 3;
              }
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        } else {
          this.$router.push({path: "/login"})
        }
      },
      unAttentionUser(userid) {
        if (this.token) {
          unfollowUser(userid).then(response => {
            let res = response.data;
            if (res.success) {
              if (this.userinfo.relationship == 1) {
                this.userinfo.relationship = 0;
              } else if (this.userinfo.relationship == 3) {
                this.userinfo.relationship = 2;
              }
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        } else {
          this.$router.push({path: "/login"})
        }
      },
      deleteAction(id, index) {//删除动态
        delDt(id).then(response => {
          let res = response.data;
          if (res.success) {
            Message({
              message: '动态已删除',
              type: 'success',
              duration: 5 * 1000
            })
            this.dtList.splice(index, 1);
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          this.$router.push({path: "/login"})
        })
      },
      deleteCp(id, index) {//删除菜谱
        delCp(id).then(response => {
          let res = response.data;
          if (res.success) {
            Message({
              message: '菜谱已删除',
              type: 'success',
              duration: 5 * 1000
            })
            this.menuList.splice(index, 1);
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          this.$router.push({path: "/login"})
        })
      },
      deleteTheme(id, index) {
        delTheme(id).then(response => {
          let res = response.data;
          if (res.success) {
            Message({
              message: '主题已删除',
              type: 'success',
              duration: 5 * 1000
            })
            this.themeList.splice(index, 1);
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          this.$router.push({path: "/login"})
        })
      },
      bookDetail(id) {
        this.$router.push({path: "/cookBook", query: {id}})
      },
      editDetail(id) {
        this.$router.push({path: "/publish", query: {id, type: 3}})
      },
      themeDetail(id) {
        this.$router.push({path: "/themeDetail", query: {id}})
      },
      editTheme(id){
        this.$router.push({path: "/publishtheme", query: {id, type: 3}})
      },
      actionDetail(id) {
        this.$router.push({path: "/dtDetail", query: {id}})
      },
    }
  }
</script>

<style>
  .el-avatar {
    margin: 0 auto;
    background-color: white;
  }

  .el-avatar > img {
    border: 1px solid #eee;
  }
  .btn-sc2 {
      position: absolute;
      top: 10px;
      right: 10px;
      width: 30px;
      height: 30px;
      padding: 0!important;
  }
</style>



